<template>
  <ul class="choice-number-list clear-fix">
    <li v-for="(item, index) in numberList" class="clear-fix" :key="index">
      <div class="top clear-fix">
        <div class="top">
          <h4>{{ item.type }}</h4>
          <div class="balls clear-fix">
            <span
              class="ball"
              v-for="(ball, innerIndex) in item.numberList"
              :class="{ selected: ball.isSelected }"
              @click="clickBall(ball, innerIndex)"
              :key="innerIndex"
            >
              {{ ball.label }}
            </span>
          </div>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
import resetBalls from "../../credit/mixins/balls-reset";
import {
  SET_SSC_SELECTED_COUNTS,
  SET_HIGHEST_PRIZE_LEVEL,
  SET_SELECTED_LINE_COUNTS
} from "../../../store/nameSpace";
import { mapGetters } from "vuex";
// 引入核心的算法文件
import { checkLotteryCounts, sortNumber } from "../../../common/js/LotteryBet";
export default {
  name: "liuhe-zodiac-template",
  mixins: [resetBalls],
  props: ["flagCount", "getShoppingCarList", "nowActiveWayInfoDetail"],
  data() {
    return {
      numberList: [],
      numberAttrNameList: [
        "isSelectAll",
        "isSelectBig",
        "isSelectSmall",
        "isSelectOdd",
        "isSelectEven"
      ],
      selectedCounts: 0
    };
  },
  computed: {
    ...mapGetters(["showYlPlay", "showLrPlay"])
  },
  methods: {
    clickBall(ball, innerIndex) {
      ball.isSelected = !ball.isSelected;
      this.calculateBetCounts(innerIndex);
    },
    calculateBetCounts(innerIndex) {
      const nowSelectNumber = [];
      const nowPrizesInfo = [];
      this.numberList.map(item => {
        const innerArr = [];
        const nowNumbers = item.numberList.length - 1;
        let count = 0;
        item.numberList.map(itemInner => {
          if (itemInner.isSelected) {
            count++;
          }
        });
        // 每行最大注数的选择拦截
        item.numberList.map(() => {
          if (count > this.nowActiveWayInfoDetail["ball_limits"]) {
            count--;
            let randIndex = Math.round(Math.random() * nowNumbers);
            while (
              innerIndex === randIndex ||
              !item.numberList[randIndex].isSelected
            ) {
              randIndex = Math.round(Math.random() * nowNumbers);
            }
            item.numberList[randIndex].isSelected = false;
          }
        });
        item.numberList.map(itemInner => {
          if (itemInner.isSelected) {
            nowPrizesInfo.push(+itemInner.prizeLevel);
            innerArr.push(itemInner);
          }
        });
        nowSelectNumber.push(innerArr);
      });

      // 重置状态管理中的最高奖级信息
      if (nowPrizesInfo.length) {
        this.$store.commit(
          SET_HIGHEST_PRIZE_LEVEL,
          `${Math.min.apply(this, nowPrizesInfo)}`
        );
      } else {
        this.$store.commit(SET_HIGHEST_PRIZE_LEVEL, ``);
      }

      const result = checkLotteryCounts({
        // 用户选择的数字
        betData: nowSelectNumber.map(item =>
          item.map(itemInner => itemInner.value)
        ),
        areaSplitChar: this.nowActiveWayInfoDetail["area_split_char"],
        ballSplitChar: this.nowActiveWayInfoDetail["ball_split_char"],
        methodDetail: this.nowActiveWayInfoDetail["count_function"]
      });
      result.labelInfo = sortNumber(
        this.nowActiveWayInfoDetail["area_split_char"],
        this.nowActiveWayInfoDetail["ball_split_char"],
        nowSelectNumber.map(item => item.map(itemInner => itemInner.label))
      );
      this.selectedCounts = result.betCount;
      this.$store.commit(SET_SSC_SELECTED_COUNTS, this.selectedCounts);
      this.$store.commit(SET_SELECTED_LINE_COUNTS, result.selectLineCounts);
      return result;
    },
    resetNumberList() {
      this.numberList.map(item => {
        item.isSelectAll = false;
        item.isSelectBig = false;
        item.isSelectSmall = false;
        item.isSelectOdd = false;
        item.isSelectEven = false;
        item.numberList.map(itemInner => {
          itemInner.isSelected = false;
        });
      });
    },
    // 将数据更新为配置文件中的相关数据
    getNumberListInfo() {
      this.numberList = [];
      const nowPlayWayInfo = this.nowActiveWayInfoDetail;
      if (
        !nowPlayWayInfo ||
        !nowPlayWayInfo["ball_strings"] ||
        !nowPlayWayInfo["ball_values"]
      )
        return false;
      const plateRows = nowPlayWayInfo["plat_rows"];

      // 获取到配置文件中需要显示label值
      let ballStrings = nowPlayWayInfo["ball_strings"].split("|");

      // 获取到配置文件中显示值对应的value
      let ballValues = nowPlayWayInfo["ball_values"].split("|");

      // 获取到配置文件中对应显示的title
      let rowTitles = nowPlayWayInfo["row_titles"].split(",");

      // 获取当前菜单详情的奖级信息
      let prizes = nowPlayWayInfo["ball_prize_levels"].split(",");

      for (let i = 0; i < plateRows; i++) {
        this.numberList.push({
          type: rowTitles[i],
          showBtn: false,
          isSelectAll: false,
          isSelectBig: false,
          isSelectSmall: false,
          isSelectOdd: false,
          isSelectEven: false,
          numberList: [],
          ylList: [],
          lrList: []
        });
      }

      ballStrings = ballStrings[0].split(",");
      ballValues = ballValues[0].split(",");

      this.numberList.map((item, index) => {
        item.type = rowTitles[index];
        for (
          let i = 0;
          i < Math.min(ballStrings.length, ballValues.length);
          i++
        ) {
          item.numberList.push({
            isSelected: false,
            label: ballStrings[i],
            value: ballValues[i],
            prizeLevel: prizes[i] ? prizes[i] : `1`
          });
        }
      });
    }
  },
  watch: {
    flagCount() {
      const result = this.calculateBetCounts();
      if (result.result !== 0) {
        return this.getShoppingCarList(false, false, 0);
      }
      this.getShoppingCarList(
        result.numberStr,
        this.selectedCounts,
        undefined,
        result.labelInfo
      );
    },
    nowActiveWayInfoDetail() {
      this.getNumberListInfo();
    }
  },
  created() {
    this.getNumberListInfo();
  }
};
</script>

<style scoped>
@import "../statics/css/plate.css";
.choice-number-list > li .top > .balls span > a {
  color: #555555;
}
</style>
